<template>
  <div class="">
    <div class="banner-box"><img src="/static/imgs/manage/index/banner.png" alt="loading" style="width: 100%" /></div>
    <div class="content">
      <modal :contentType="modal.content" v-show="modal.show" v-on:toggleModal="toggleModal">
        <div class="bind-header">
          <p class="t1">账户信息完善</p>
          <p class="t2">为了您的使用方便和账号安全，请完善以下信息</p>
        </div>
        <div class="params-box">
          <ul>
            <li>
              <span class="name">机构名称：</span>
              <div class="input-box">
                <input type="text" placeholder="请输入机构名称" />
              </div>
            </li>
            <li>
              <span class="name">手机绑定：</span>
              <div class="input-box">
                <input type="text" placeholder="请输入手机号码" />
              </div>
            </li>
            <li>
              <span class="name"></span>
              <div class="input-box">
                <input type="text" placeholder="请输入验证码" />
                <button type="button" class="code-btn">发送验证码</button>
              </div>
            </li>
            <li>
              <span class="name">登录密码：</span>
              <div class="input-box">
                <input type="text" placeholder="请输入6位以上新密码" />
              </div>
            </li>
            <li class="ope">
              <span class="name"></span>
              <div class="input-box">
                <button type="button" class="no">取消</button>
                <button type="button" class="yes">确定</button>
              </div>
            </li>
          </ul>
          <div class="no-again">
            <span class="checkbox"></span>不再提示</div>
        </div>
      </modal>

      <div class="user-box border-d9 shadow">
        <div class="user-head-img">
          <div class="add">
            <input type="file" @change="fileUserImg">
          </div>
          <div class="img-show"></div>
          <div class="user-name">
            <p class="name">悟空编程</p>
            <p class="say-hello">Hi
              <span>{{userInfo.userName}}</span>, 早上好!</p>
            <p class="time">
              <span class="useful">有效期：2017-06-02 20:41 至 2018-06-02 20:41</span>
              <!-- <span class="warn">您的试用时间还有 7 天，马上升级！</span> -->
            </p>
            <div class="btn">
              <div class="btn-left">
                <img src="/static/imgs/manage/icon-vip.png" style="width: 16px;height: 16px" class="icon-level" />
                <span>免费体验版</span>
              </div>
              <el-button type="danger" plain size="medium" class="up-grade">升级</el-button>
              <!--<a class="lv lv1">月付费会员</a>-->
              <!--<a class="lv lv2">年付费会员</a>-->
            </div>
          </div>
        </div>
        <ul class="extra adviser">
          <li class="t">我的必火管家：</li>
          <li class="li who">Ruby 为您服务</li>
          <li class="li contact">
            186 7486 8812
            <div class="icon-wx" @mouseover="togglewxShow" @mouseout="togglewxClose">
              <div class="wx" v-show="contact.wxShow">
                <img src="/static/imgs/temp/图层 16@2x.png" alt="" />
                <p>扫我加技术顾问微信</p>
              </div>
              <input type="text" class="none" ref="icon_wx_input" @blur="togglewxShow" />
            </div>
          </li>
        </ul>
        <ul class="extra">
          <div class="extra-li">
            <li class="t">增值服务：</li>
            <li class="li">
              <a href="#">短信包
                <span>5000条</span>
              </a>
              <a href="#">广告位</a>
              <a href="#">流量主</a>
              <a href="#">营销培训</a>
              <a href="#">更多></a>
            </li>
          </div>
        </ul>
      </div>

      <ul class="operate flexBox">
        <li class="border-d9 b1" @click="toNew">
          <div class="contain">
            <span class="icon2 icon-create"></span>
            <p class="t">创建活动</p>
            <p class="text">快来创建一个活动吧</p>
          </div>
        </li>
        <li class="border-d9 b2">
          <div class="contain">
            <span class="icon2 icon-share"></span>
            <p class="t">分享有礼</p>
            <p class="text">分享我的活动给朋友</p>
          </div>
        </li>
        <li class="border-d9 b3" @click="toPay">
          <div class="contain">
            <span class="icon2 icon-spend"></span>
            <p class="t">进入充值</p>
            <p class="text">目前余额：
              <span class="c-main arial ">10</span> 元</p>
          </div>
        </li>
      </ul>

      <div class="my-activities border-d9 shadow">
        <p class="title">我的活动
          <a href="#" class="more">查看更多</a>
        </p>
        <div class="con">
          <div class="con-background"></div>
          <div class="left-time">
            <span>34天后结束</span>
            <img src="/static/imgs/temp/图层 14@2x.png" alt="" class="last-img" />
          </div>
          <div class="params-box">
            <div>
              <span class="port-center">砍价</span>
              <span class="port-title">这里显示此次活动标题</span>
            </div>
            <p class="port-time">起始时间：2018-1-10 15:30:24 至 2018年-2-10 15:30:24</p>
            <ul class="port-con flexBox">
              <li class="act">活动编辑</li>
              <li class="act2">数据核销</li>
              <li class="act3">群发短信</li>
              <li class="act4">渠道链接</li>
              <li class="act5">我要推广</li>
            </ul>
            <ul class="port-data flexBox">
              <li>
                <p>浏览量</p>
                <span class="c-main">543</span>
              </li>
              <li>
                <p>参与人数</p>
                <span class="">543</span>
              </li>
              <li>
                <p>报名人数</p>
                <span class="">543</span>
              </li>
              <li>
                <p>转化率</p>
                <span class="">54%</span>
              </li>
            </ul>
          </div>
          <ul class="action flexBox">
            <li class="copy-link">
              <!-- <span></span> -->
              <el-tooltip placement="top">
                <div slot="content">预览活动</div>
                <span></span>
              </el-tooltip>
            </li>
            <li class="copy-port">
              <el-tooltip placement="top">
                <div slot="content">复制活动</div>
                <span></span>
              </el-tooltip>
            </li>
            <li class="delete-port">
              <el-tooltip placement="top">
                <div slot="content">删除活动</div>
                <span></span>
              </el-tooltip>
            </li>
          </ul>
        </div>
      </div>

      <div class="recommend-activities border-d9 shadow">
        <p class="title">推荐活动
          <a href="#" class="more">查看更多</a>
        </p>
        <act-list class="bottom-list" :activities="recommendActs" type="show"></act-list>
        <div class="recommend-activities_tip">
          <p class="loading-text" v-show="requestLoading"><i class="el-icon-loading"></i>必火君玩命加载中...</p>
          <p class="requestStateTip" v-show="!requestLoading && recommendActs.length === 0">没有找到相关的数据哟!</p>   
        </div>
      </div>

    </div>
  </div>
</template>

<script>
import User from 'src/server/user'
import Activity from 'src/server/activity'
import fileUpload from 'src/server/fileUpload'
import { mapGetters } from 'vuex'
import ActList from 'src/components/act-list'

export default {
  data () {
    return {
      modal: {
        show: false,
        content: 'bind-phone'
      },
      recommendActs: [], // 推荐活动
      myActs: {}, // 我的活动
      contact: {
        // 技术顾问
        wxShow: false
      },
      requestLoading: false // 是否正在请求数据

    }
  },
  components: {
    ActList
  },
  computed: {
    ...mapGetters('user', {
      userInfo: 'userInfo'
    })
  },
  mounted () {
    // 获取我的活动
    Activity.myActs({
      data: {
        status: 0,
        innerType: 1
      }
    }).then(data => {
      this.myActs = data.rs.pageList
    }).catch(err => {
      console.log(err)
    })
    // 获取推荐活动
    this.requestLoading = true
    Activity.recommend().then(data => {
      if (data.suc) {
        this.recommendActs = data.rs.recommendActivityList
      }
    }).catch(err => {
      console.log(err)
    }).finally(() => {
      this.requestLoading = false
    })
    User.qrcode({ content: 'https://www.baidu.com/' }).then(res => {
      console.log(res.rs)
    }).catch(err => {
      console.log(err)
    })
    this.userInfo.id ||
      User.self().then(res => {
        console.log(res)
        if (!res.suc) {
          this.$message({ type: 'error', message: '获取信息失败' })
          location.href = 'index.html'
        } else {
          this.$store.commit('user/UPDATE_USERINFO', res.rs)
        }
      })
  },
  methods: {
    toNew () {
      // 跳转到新建活动页面
      this.$router.push({
        path: '/nav-new'
      })
    },
    toPay () {
      // 跳转到充值页面
      this.$router.push({
        path: '/nav-replenish'
      })
    },
    togglewxShow () {
      this.contact.wxShow = true
    },
    togglewxClose () {
      this.contact.wxShow = false
    },
    toggleModal () {
      this.modal.show = !this.modal.show
    },
    fileUserImg (e) {
      let file = e.target.files[0]
      let formData = new FormData()
      formData.append('imgType', 'user_icon')
      formData.append('file', file)
      console.log({ imgType: file.type, file: file })
      let params = {
        data: formData,
        methods: 'post',
        baseURL: 'http://1655w941f1.51mypc.cn:18888'
      }
      fileUpload.imgUpload(params).then(res => {
        console.log(res)
        if (res.suc) {
          console.log(res.rs)
        } else {
          console.log(res.msg)
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped src="common/style/module/manage/index.scss"></style>



